const Layout = ( props ) => {
  const { pageTitle, canonical, children } = props

    return (
      <div className={`container`}>
        {children}
      </div>
    )
}

Layout.propTypes = {
  pageTitle: PropTypes.string,
  canonical: PropTypes.string,
}

export default Layout